___CSS_LOADER_EXPORT___.push([module.id, ".isFullPageCss{background:#000c}", "",{"version":3,"sources":["webpack://./resources/js/Components/Navigation/NavigationMenu.vue"],"names":[],"mappings":"AAgWA,eACI,gBAEJ","sourcesContent":["<template>\n    <div class=\"hidden lg:block fixed top-0 w-full nav-mask border-b-2 border-gray-100\">\n    <nav class=\"sticky top-0 bg-black\" :class=\"{ isFullPageCss: videoPlayerStore.fullPage }\">\n        <!-- Primary Navigation Menu -->\n        <div class=\"mx-auto px-4 lg:px-6 xl:px-8\">\n            <div class=\"flex justify-between h-16\">\n                <div class=\"flex\">\n                    <!-- Logo -->\n                    <div class=\"shrink-0 flex items-center\">\n                        <Link @click=\"navigateToStream\">\n                            <JetApplicationMark class=\"block h-9 w-auto\"/>\n                        </Link>\n                    </div>\n\n                    <!-- Navigation Links -->\n                    <div class=\"hidden space-x-8 lg:-my-px lg:ml-10 lg:flex\">\n                        <h3 class=\"inline-flex items-center relative\">\n                            <JetNavLink\n                                v-touch=\"()=>(navigateToStream())\"\n                                @click=\"navigateToStream()\"\n                                :active=\"userStore.currentPage === 'stream'\">\n                                Stream\n                            </JetNavLink>\n                        </h3>\n                        <h3 class=\"inline-flex items-center relative\">\n                        <JetNavLink\n                            v-touch=\"()=>(route('schedule'))\"\n                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                            :href=\"route('schedule')\"\n                            :active=\"userStore.currentPage === 'schedule'\">\n                                Schedule\n                        </JetNavLink>\n                        </h3>\n                        <h3 class=\"inline-flex items-center relative\">\n                        <JetNavLink\n                            v-touch=\"()=>(route('news'))\"\n                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                            :href=\"route('news')\"\n                            :active=\"userStore.currentPage === 'news'\">\n                                News\n                        </JetNavLink>\n                        </h3>\n                        <h3 class=\"inline-flex items-center relative\"\n                            v-if=\"userStore.isSubscriber || userStore.isVip || userStore.isAdmin\">\n                        <JetNavLink\n                            v-touch=\"()=>(route('movies'))\"\n                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                            :href=\"route('movies')\"\n                            :active=\"userStore.currentPage === 'movies'\">\n                                Movies\n                        </JetNavLink>\n                        </h3>\n                        <h3 class=\"inline-flex items-center relative\">\n                        <JetNavLink\n                            v-touch=\"()=>(route('shows'))\"\n                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                            :href=\"route('shows')\"\n                            :active=\"userStore.currentPage === 'shows'\">\n                                Shows\n                        </JetNavLink>\n                        </h3>\n                        <h3 class=\"inline-flex items-center relative\">\n                        <JetNavLink\n                            v-touch=\"()=>(route('shop'))\"\n                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                            :href=\"route('shop')\"\n                            :active=\"userStore.currentPage === 'shop'\">\n                                Shop\n                        </JetNavLink>\n                        </h3>\n                        <h3 class=\"inline-flex items-center relative\"\n                            v-if=\"userStore.isVip || userStore.isAdmin\">\n                        <JetNavLink\n                            v-touch=\"()=>(route('library'))\"\n                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                            :href=\"route('library')\"\n                            :active=\"userStore.currentPage === 'library'\">\n                                My Library\n                            <div class=\"text-xs text-white bg-yellow-800 uppercase flex justify-center items-center ml-1 -right-4 top-1.5\n                                    font-semibold inline-block py-0.5 px-1 rounded last:mr-0 mr-1\">\n                                coming soon\n                            </div>\n                        </JetNavLink>\n                        </h3>\n                    </div>\n\n                </div>\n\n\n                <div class=\"flex flex-row space-x-4 pt-2\">\n\n                        <div class=\"flex-item align-text-top mt-2 mx-6\">\n                          <NotificationButton />\n                        </div>\n\n                        <div v-if=\"userStore.isCreator\" class=\"flex-item align-self-center text-yellow-600 uppercase hidden md:block text-xs w-fit\">\n                            GOAL <span class=\"text-sm font-semibold\">100</span> subscribers\n                        </div>\n                        <div class=\"flex-item\">\n                            <div v-if=\"!userStore.isAdmin && !userStore.isVip && !userStore.isSubscriber\">\n                                <JetNavLink v-touch=\"()=>(route('upgrade'))\"\n                                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                            :href=\"route('upgrade')\"\n                                            :active=\"userStore.currentPage === 'upgrade'\"\n                                            class=\"active:border-none hover:border-none focus:border-none border-none\">\n                                    <div class=\"w-full rounded-lg p-2 bg-gray-100 text-black hover:bg-gray-300 hover:text-green-900\">UPGRADE NOW</div>\n                                </JetNavLink>\n                            </div>\n                                <div v-if=\"userStore.isSubscriber && !userStore.isAdmin\" class=\"text-fuchsia-700\">PREMIUM</div>\n                                <div v-if=\"userStore.isVip && !userStore.isAdmin\" class=\"text-fuchsia-700\">VIP</div>\n                                <div v-if=\"userStore.isCreator && userStore.isSubscriber && !userStore.isAdmin\" class=\"text-fuchsia-700\">CREATOR</div>\n                            <div v-if=\"userStore.isAdmin\" class=\"text-red-700\">ADMIN</div>\n                        </div>\n                    <!-- Settings Dropdown -->\n                    <div class=\"relative menuMask w-fit\">\n\n                        <JetDropdown align=\"right\" width=\"48\">\n                            <template #trigger>\n                                <button v-if=\"$page.props.jetstream.managesProfilePhotos\"\n                                        class=\"flex text-sm border-2 border-transparent rounded-full focus:outline-none focus:border-gray-300 transition\">\n                                    <img class=\"min-h-[2.5rem] min-w-[2.5rem] max-w-[2.5rem] rounded-full object-cover\"\n                                         :src=\"$page.props.user.profile_photo_url\" :alt=\"$page.props.user.name\">\n                                </button>\n\n                                <span v-else class=\"inline-flex rounded-md\">\n                                            <button type=\"button\"\n                                                    class=\"inline-flex items-center px-3 py-2 border border-transparent text-sm leading-4 font-medium rounded-md text-gray-500 bg-white hover:text-gray-700 focus:outline-none transition\">\n                                                {{ $page.props.user.name }}\n\n                                                <svg\n                                                    class=\"ml-2 -mr-0.5 h-4 w-4\"\n                                                    xmlns=\"http://www.w3.org/2000/svg\"\n                                                    viewBox=\"0 0 20 20\"\n                                                    fill=\"currentColor\"\n                                                >\n                                                    <path fill-rule=\"evenodd\"\n                                                          d=\"M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z\"\n                                                          clip-rule=\"evenodd\"/>\n                                                </svg>\n                                            </button>\n                                        </span>\n                            </template>\n\n                            <template #content>\n                                <div class=\"divide-y\">\n\n                                    <div class=\"text-xs text-gray-800 py-2 ml-4\">Hello, {{ $page.props.user.name }}</div>\n\n                                    <div class=\"pt-2 pb-3\">\n\n                                        <JetDropdownLink\n                                            v-if=\"userStore.isCreator\"\n                                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                            :href=\"route('dashboard')\">\n                                            Dashboard\n                                        </JetDropdownLink>\n\n                                        <JetDropdownLink\n                                            v-if=\"userStore.isNewsPerson\"\n                                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                            :href=\"route('newsroom')\">\n                                            Newsroom\n                                        </JetDropdownLink>\n\n                                        <JetDropdownLink\n                                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                            :href=\"route('profile.show')\">\n                                            Settings\n                                        </JetDropdownLink>\n\n                                        <JetDropdownLink\n                                            v-if=\"userStore.hasAccount\"\n                                            @click=\"billingPortal\">\n                                            Account\n                                        </JetDropdownLink>\n\n                                        <JetDropdownLink\n                                            v-if=\"userStore.isCreator\"\n                                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                            :href=\"route('training')\">\n                                            Training\n                                        </JetDropdownLink>\n\n                                        <JetDropdownLink\n                                            v-if=\"userStore.isCreator\"\n                                            @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                            :href=\"route('videoupload')\">\n                                            Video Upload\n                                        </JetDropdownLink>\n\n<!--                                        &lt;!&ndash; Creator Only Links &ndash;&gt;-->\n<!--                                        <div v-if=\"userStore.isCreator\">-->\n<!--                                            <div class=\"border-t border-1 mt-3 border-gray-300 block px-4 py-2 text-xs text-gray-400\">-->\n<!--                                                Creator Only Links-->\n<!--                                            </div>-->\n\n<!--                                        </div>-->\n\n                                        <!-- Admin Only Links -->\n                                        <div v-if=\"userStore.isAdmin\">\n                                            <div class=\"border-t border-1 mt-3 border-gray-300 block px-4 py-2 text-xs text-gray-400\">\n                                                Admin Only Links\n                                            </div>\n\n                                            <JetDropdownLink\n                                                @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                                :href=\"route('admin.settings')\">\n                                                Admin Settings\n                                            </JetDropdownLink>\n\n                                            <JetDropdownLink\n                                                @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                                :href=\"route('admin.channels')\">\n                                                Channels\n                                            </JetDropdownLink>\n\n                                            <JetDropdownLink\n                                                @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                                :href=\"route('calculations')\">\n                                                Calculations\n                                            </JetDropdownLink>\n\n                                            <JetDropdownLink\n                                                @click=\"videoPlayerStore.makeVideoTopRight()\"\n                                                :href=\"route('mistServerApi')\">\n                                                MistServer API\n                                            </JetDropdownLink>\n\n                                        </div>\n                                        <!-- Authentication -->\n                                        <form @submit.prevent=\"logout\">\n\n                                            <JetDropdownLink as=\"button\">\n                                                Log Out\n                                            </JetDropdownLink>\n                                        </form>\n\n                                    </div>\n                                    <div class=\"border-t border-gray-100\">\n\n                                    </div>\n\n                                    <div class=\"grid grid-col-1 w-full text-gray-400 text-sm py-2\">\n                                        <AppVersion />\n                                    </div>\n\n                                </div>\n                            </template>\n                        </JetDropdown>\n                    </div>\n                </div>\n\n\n\n            </div>\n        </div>\n    </nav>\n    </div>\n</template>\n\n<script setup>\nimport { computed } from \"vue\"\nimport JetApplicationMark from '@/Jetstream/ApplicationMark'\nimport JetDropdownLink from '@/Jetstream/DropdownLink'\nimport { Link } from '@inertiajs/inertia-vue3'\nimport NotificationsButton from \"@/Components/Navigation/NotificationsButton\"\nimport JetDropdown from '@/Jetstream/Dropdown'\nimport JetNavLink from '@/Jetstream/NavLink'\nimport {Inertia} from \"@inertiajs/inertia\"\nimport { useVideoPlayerStore } from \"@/Stores/VideoPlayerStore\"\nimport { useChatStore } from \"@/Stores/ChatStore\"\nimport { useStreamStore } from \"@/Stores/StreamStore\"\nimport { useUserStore } from \"@/Stores/UserStore\"\nimport { useWelcomeStore } from \"@/Stores/WelcomeStore\"\nimport AppVersion from \"@/Components/AppVersion/AppVersion.vue\";\nimport NotificationButton from \"@/Components/Notifications/NotificationButton.vue\";\n\n\nlet chat = useChatStore();\nlet videoPlayerStore = useVideoPlayerStore();\nlet streamStore = useStreamStore()\nlet userStore = useUserStore()\nlet welcomeStore = useWelcomeStore()\n\nlet props = defineProps({\n    user: Object,\n})\n\n// function loadStreamPage() {\n//     videoPlayerStore.makeVideoFullPage()\n//     userStore.showNavDropdown = false\n// }\n\nconst logout = () => {\n    Inertia.post(route('logout'), {}, {\n        onSuccess: () => {\n            // Reset state inside onSuccess callback\n            resetStores();\n            window.location.reload(); // Force a page reload\n        }\n    });\n};\n//     videoPlayerStore.mute();\n//     videoPlayerStore.fullPage = true;\n//     videoPlayerStore.loggedIn = false;\n//     videoPlayerStore.ottChat = false;\n//     videoPlayerStore.ott = 0;\n//     videoPlayerStore.class = \"videoBgFull\";\n//     videoPlayerStore.videoContainerClass = \"videoContainerBgFull\";\n//     userStore.showNavDropdown = false;\n//     userStore.clearUserData()\n// };\n\nconst resetStores = () => {\n    // Reset logic for your stores\n    videoPlayerStore.mute();\n    videoPlayerStore.fullPage = true;\n    videoPlayerStore.loggedIn = false;\n    videoPlayerStore.ottChat = false;\n    videoPlayerStore.ott = 0;\n    videoPlayerStore.class = \"welcomeVideoClass\";\n    videoPlayerStore.videoContainerClass = \"welcomeVideoContainer\";\n    userStore.showNavDropdown = false;\n    userStore.clearUserData();\n};\n\n// let isStreamPage = false\n//\n// function setPage() {\n//     if (videoPlayerStore.currentPage = \"stream\") {\n//         videoPlayerStore.currentPageIsStream = true;\n//     } else\n//         videoPlayerStore.currentPageIsStream = false;\n// }\n\n// setPage()\n\nfunction billingPortal() {\n    location.href = ('https://not.tv/billing-portal')\n}\n\nfunction navigateToStream() {\n    videoPlayerStore.makeVideoFullPage()\n    videoPlayerStore.ott = 0\n    userStore.showNavDropdown = false\n    userStore.prevUrl = window.history.state.url\n    Inertia.visit(`/stream`)\n}\n\n</script>\n<style>\n\n.isFullPageCss {\n    background: rgba(0, 0, 0, 0.8);\n    /*background: yellow;*/\n}\n\n</style>\n"],"sourceRoot":""}]);
